import React from "react";

function Home({ onDecrement, onIncrement, counter }) {
  return (
    <div>
      <h1 id="counter">Counter: {counter}</h1>
      <button id="increment" onClick={onIncrement}>
        Increment
      </button>
      <button id="decrement" onClick={onDecrement}>
        Decrement
      </button>
    </div>
  );
}

export default Home;



